export default function Key({value, hidden}) {
    const keymap = {
        "p": ["p"],
        "b": ["b"],
        "": ["+", "f"],
        "": ["+", "b"],
        "t": ["t"],
        "d": ["d"],
        "c": ["c"],
        "": ["+", "j"],
        "": ["c"],
        "": ["c"],
        "": ["z"],
        "": ["j"],
        "k": ["+", "k"],
        "g": ["g"],
        "": ["+", "g"],
        "f": ["f"],
        "v": ["v"],
        "": ["+", "t"],
        "": ["+", "d"],
        "": ["+", "t"],
        "": ["+", "d"],
        "s": ["s"],
        "z": ["z"],
        "": ["-", "s"],
        "": ["-", "z"],
        "": ["+", "s"],
        "": ["+", "z"],
        "m": ["m"],
        "": ["+", "m"],
        "n": ["n"],
        "": ["+", "n"],
        "": ["+", "n"],
        "": ["+", "n"],
        "": ["q", "n"],
        "x": ["x"],
        "": ["+", "x"],
        "h": ["h"],
        "": ["b", "h"],
        "": ["q", "r"],
        "": ["q", "R"],
        "": ["q", "b"],
        "": ["q", "l"],
        "q": ["q"],
        "": ["q", "q"],
        "": ["b", "b"],
        "": ["b", "d"],
        "": ["b", "s"],
        "": ["b", "g"],
        "": ["b", "G"],
        "w": ["w"],
        "": ["v"],
        "": ["r"],
        "l": ["l"],
        "": ["r", "r"],
        "": ["r", "l"],
        "j": ["j"],
        "": ["l", "j"],
        "a": ["a"],
        "i": ["i"],
        "y": ["y"],
        "":["/", "i"],
        "":["/", "i"],
        "e": ["e"],
        "": ["/", "o"],
        "": [""],
        "o": ["o"],
        "": ["w"],
        "": ["w"],
        "u": ["-", "u"],
        "": ["-", "i"],
        "": ["-", "y"],
        "": ["-", "u"],
        "": ["-", "e"],
        "": ["o"],
        "": [""],
        "": ["-", "o"],
        "": ["O"],
        "": ["a"],
        "": ["a"],
    };
    let keys = keymap[value] || [value];
    if (value == "") {return null;}
    return !hidden &&
        <div className="key-tooltip">
            {keys.map((key) => <div className="key-background">
                <div className="key">{key}</div>
            </div>)}
        </div>;
}
