function IPAEntry({value, colSpan}) {
    const [hidden, setHidden] = useState(true);
    return <td
        colSpan={colSpan}
        onMouseEnter={() => setHidden(false)}
        onMouseLeave={() => setHidden(true)}>
        <div className="IPAEntry">
            {value}
            <Key value={value} hidden={hidden} />
        </div>
    </td>;
}

